:root {
  --blue: #00acdc;
}

body {
  margin: 0;
  font-family: Roboto, sans-serif;
}

.header {
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.logo {
  width: 40px;
  height: 40px;
  display: block;
}

.nav-list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav-item {
  margin-left: 20px;
}

.nav-item:first-child {
  color: var(--blue);
  margin-left: 0;
}

.nav-link {
  text-transform: uppercase;
  white-space: nowrap;
  color: #000;
  cursor: pointer;
  align-items: center;
  height: 60px;
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
  display: flex;
  position: relative;
}

.nav-link:hover, .nav-link.is-active {
  color: var(--blue);
}

.nav-link.is-active:after {
  content: "";
  background-color: var(--blue);
  border-radius: 8px;
  width: 37px;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}
